<template>
    <view-port>
        <ion-input class="his-lg-text ion-text-center input_display" readonly :value="time"/>
        <ion-grid class="his-floating-keyboard">
            <ion-row> 
                <ion-col sm-size="12"> 
                    <picker-selector
                        label="Hours"
                        :value="formattedHours"
                        @onValue="pickHours"
                        @onIncrement="addHours"
                        @onDecrement="subtractHours"
                    />
                </ion-col>
                <ion-col sm-size="12"> 
                    <picker-selector
                        label="Minutes"
                        :value="formattedMinutes"
                        @onValue="pickMinutes"
                        @onIncrement="addMinutes"
                        @onDecrement="subtractMinutes"
                    />
                </ion-col>
            </ion-row>
            <ion-row>
                <ion-col class="ion-text-center" > 
                    <ion-button @click="timeNow" color="success" style="width:100%; height:6vh;"> 
                        <b>Now</b>
                    </ion-button>
                </ion-col>
            </ion-row>
        </ion-grid>
    </view-port>
</template>
<script setup lang="ts">
import { ref, computed, onActivated, watch } from "vue";
import ViewPort from "@/components/DataViews/ViewPort.vue"
import PickerSelector from "@/components/Selectors/PickerSelector.vue"
import { AppendleadingZero } from "@/utils/Strs";
import { IonGrid, IonInput, IonCol, IonRow, IonButton } from '@ionic/vue'
import { optionsActionSheet } from "@/utils/ActionSheets"

const emit = defineEmits(['onValue', 'onFieldValueActivated'])

const hours = ref<number>(0)

const minutes = ref<number>(0)

const formattedHours = computed(() => AppendleadingZero(hours.value) as string)

const formattedMinutes = computed(() => AppendleadingZero(minutes.value) as string)

const time = computed(() => `${formattedHours.value}:${formattedMinutes.value}`)

async function showNumbers(context: string, max: number) {
    const numbers: any = Array.from({ length: max }).map((_, index) => AppendleadingZero(index))
    const modal = await optionsActionSheet(
        context,
        '',
        numbers,
        []
    )
    return modal.selection
}

onActivated(() => {
    emit('onFieldValueActivated', this)
})

watch(time, (time) => {
    const timestr = `${time}:00`
    emit('onValue', { 
        label: timestr, 
        value: timestr
    })
}, { deep: true })

function timeNow() {
    const now = new Date();
    minutes.value = now.getMinutes();
    hours.value = now.getHours();
}

function addHours() {
    if (hours.value >= 23) {
        hours.value = 0
    } else {
        ++hours.value
    }
}

function subtractHours() {
    if (hours.value > 0) {
        --hours.value
    } else {
        hours.value = 23
    }
}

function addMinutes() {
    if (minutes.value >= 59) {
        addHours()
        minutes.value = 0
    } else {
        minutes.value += 1
    }
}

function subtractMinutes() {
    if (minutes.value > 0) {
        --minutes.value
    } else {
        subtractHours()
        minutes.value = 59
    }
}

async function pickHours() {
    const num = await showNumbers('Hours', 24)
    if (num) hours.value = parseInt(num)
}

async function pickMinutes() {
    const num = await showNumbers('Minutes', 60)
    if (num) minutes.value = parseInt(num)
}
</script>